.Wrapper {
  margin-bottom: 1rem; }

.Label {
  font-size: 0.9em; }

.Select {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background: #EEEEEE;
  border: 1px solid transparent;
  letter-spacing: normal;
  line-height: 1rem;
  margin: 0;
  padding: 0.5rem 1rem;
  position: relative;
  text-transform: none;
  width: 100%; }



